.slider{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 600px;
    height: 400px;
    position: relative;
}

.slide{
    border-radius: 0.5rem;
    box-shadow: 0 0 7px #666;
    height: 100%;
    width: 100%;
}

.slide-hidden{
    display: none;
}

.arrow{
    position: absolute;
    width: 2rem;
    height: 2rem;
    color: white;
    /* box-shadow: 0 0 10px black; */
    filter: drop-shadow(0 0 5px #555);
}

.arrow:hover{
    cursor:pointer;
}

.arrow-left{
    left: 1rem;
}

.arrow-right{
    right: 1rem;
}

.indicators{
    display: flex;
    position: absolute;
    bottom: 1rem;
}

.indicator{
    background-color: white;
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 100%;
    border: none;
    outline: none;
    box-shadow: 0 0 5px #555;
    margin: 0 0.2rem;
    cursor: pointer;
}

.indicator-inactive{
    background-color: gray;
}